import React from "react";
import { Fade } from "react-reveal";
import "./GithubRepoCard.css";

export default function GithubRepoCard({ repo, isDark }) {
	function openRepoinNewTab(url) {
		var win = window.open(url, "_blank");
		win.focus();
	}

	return (
		<Fade bottom duration={1000} distance="20px">
			<div>
				<div className={isDark ? "dark-card-mode repo-card-div" : "repo-card-div"} onClick={() => openRepoinNewTab(repo.link)}>
					<div className="repo-name-div">
						<p className="repo-name">{repo.repo}</p>
					</div>

					<p className="repo-description">{repo.description}</p>

					<div className="repo-stats">
						<div className="repo-left-stat">
							{repo.language !== null && (
								<span>
									<div className="language-color" style={{ backgroundColor: repo.language.color }}></div>
									<p>{repo.language}</p>
								</span>
							)}

							<span>
								<svg aria-hidden="true" className="octicon" height="20" role="img" viewBox="0 0 10 16" width="12" fill="rgb(106, 115, 125)" className="repo-star-svg">
									<path fillRule="evenodd" d="M8 1a1.993 1.993 0 0 0-1 3.72V6L5 8 3 6V4.72A1.993 1.993 0 0 0 2 1a1.993 1.993 0 0 0-1 3.72V6.5l3 3v1.78A1.993 1.993 0 0 0 5 15a1.993 1.993 0 0 0 1-3.72V9.5l3-3V4.72A1.993 1.993 0 0 0 8 1zM2 4.2C1.34 4.2.8 3.65.8 3c0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3 10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3-10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2z" />
								</svg>
								<p>{repo.forks}</p>
							</span>

							<span>
								<svg aria-hidden="true" className="octicon" height="20" role="img" viewBox="0 0 14 16" width="14" fill="rgb(106, 115, 125)" className="repo-star-svg">
									<path fillRule="evenodd" d="M14 6l-4.9-.64L7 1 4.9 5.36 0 6l3.6 3.26L2.67 14 7 11.67 11.33 14l-.93-4.74L14 6z" />
								</svg>
								<p>{repo.stars}</p>
							</span>
						</div>
					</div>
				</div>
			</div>
		</Fade>
	);
}
